import React from 'react';
import { Schedule, InsertInvitation, LocalHospital, Call } from '@material-ui/icons'
import Card from './Card';
function Cards() {
    return (
        <div className="cards">
            <Card logo={Schedule} title="OPENING HOURS">
                <ul>
                    <li>
                        <p>Mon-Fri</p>
                        <p>8:00am-7:00pm</p>
                    </li>
                    <li>
                        <p>Saturday</p>
                        <p>9:00am-5:00pm</p>
                    </li>
                    <li>
                        <p>Sunday</p>
                        <p>9:00am-3:00pm</p>
                    </li>
                </ul>
            </Card>
            <Card logo={InsertInvitation} title="DOCTORS’ TIMETABLE">
            The following is for guidance only to help you plan your appointment with a preferred doctor or nurse. It does not guarantee availability as the doctors or nurses may sometimes be attending to other duties.
            </Card>
            <Card logo={Call} title="APPOINTMENTS">
            The first step towards a healthy life is to schedule an appointment. Please contact our office by phone or complete the appointment request form.
            </Card>
            <Card logo={LocalHospital} title="EMERGENCY CASES">
            9931165095
Call us!
            </Card>
        </div>
    )
}

export default Cards
