import React from 'react'
import TestCard from './TestCard';
import testTube from '@iconify-icons/mdi/test-tube';
import microscopeIcon from '@iconify-icons/mdi/microscope';
import bxWorld from '@iconify-icons/bx/bx-world';


function Test() {
    return (
        <div className="test">
            <TestCard icon={testTube} title="Tests" detail="A laboratory processing over 60,000 samples and above 2.5 lakhs investigations everyday." />
            <TestCard icon={microscopeIcon} title="Technologies" detail="Thyrocare has made right use of the advanced superior technology, capable of working on its unbelievable volumes and yet deliver quality and precision to its customers." />
            <TestCard icon={bxWorld} title="Automation" detail="With best brands of automated analyzers, barcoded system, bi-directional interfacing, total laboratory automation; Thyrocare succeeded in delivering quality reports." />
        </div>
    )
}

export default Test;
