import React from 'react'
import { IconButton } from '@material-ui/core'
import { Call,LocationOn, ContactPhone }  from '@material-ui/icons'
function Navbar() {
  const bottom = () => {
     window.scrollTo({
      top:document.body.scrollHeight,
      left: 100,
      behavior: 'smooth'
     });
  };
    return (
        <div className="Navbar">
            <ul className="title">
                <li>
                    <p>Thyrocare</p>
                    <p className="sm" >KEDIA DIAGNOSTIC</p>
                </li>
            </ul>
            <ul className="small" onClick={bottom} >
            <IconButton>
              <ContactPhone fontSize="large"/>
            </IconButton>
            </ul>
            <ul className="detail">
                <li>
                <IconButton>
                  <Call fontSize='large' color='white' htmlColor="white" />
                </IconButton>
                   9931165095
                </li>
                <li>
                <IconButton>
                  <LocationOn fontSize='large' color='white' htmlColor="white" />
                </IconButton>
                  KEDIA DIAGNOSTIC, Opp. Rose the Medicity ( Near Dr. Shukla Sir ) DighiWest, Darbhanga-04 
                </li>
            </ul>
        </div>
    )
}

export default Navbar
