import React from 'react'

function ServiceCard(props) {
    const {data} = props;
    return (
        <div className="service-card" onClick={()=>{props.service(data)}}>
            <p className="title">{data.title}</p>
            <p className="parameter">Test Parameters: {data.para}</p>
            <p className="price">{'\u20B9'} {data.price}/-</p>
            <p className="book">{data.num} booked in last week</p>
            <button><a href="https://wa.me/919334445501" target="_blank" rel="noreferrer" >Book Now</a></button>
        </div>
    )
}

export default ServiceCard
