import React from 'react';
import doctor from '../assets/doctor.png';
import { HourglassEmpty,PlaylistAdd } from '@material-ui/icons'
import wa from '../assets/5 (2).png';
import logo from '../assets/trans-logo.png'
function ServiceDetail(props) {
    const {data} =props;
    return (
        <div className="service-detail">
        <div className="top">
            <div className="appoint"> 
               <p className="title">Qualified Team of Experts</p>
               <p className="detail">Our Team of diagnosticians is always ready to help you be Healthier</p>
               <div className="button" ><a href="https://wa.me/919334445501" rel="noreferrer" target="_blank" ><img src={wa} alt="whatsapp" /> Make An Appointment</a></div>
               <div className="logo"><img src={logo} alt="logo" /></div>
            </div>
            <div className="doctor">
                <img src={doctor} alt="doctor" />
            </div>
            <div>
            </div>
           
        </div>
        <div className="details" >
           <div><HourglassEmpty fontSize="large" />{data.title}</div>
           <div>{'\u20B9'} {data.price}</div>
           <div><PlaylistAdd fontSize="large" />{data.num} booked in last week</div>
           <div><button><a href="https://wa.me/919334445501" target="_blank" rel="noreferrer" >Book Now</a></button></div>
       </div>
        </div>
    )
}

export default ServiceDetail;
